import './GameCard.css';

const GameCard = ({cardData, index, clickHandler}) => {
  const { isClosed, url, name } = cardData;
  return <div className="picture" onClick={() => clickHandler(index)}>
    <img 
      alt={index}
      className='cardImage'
      src={url}
    />
    {isClosed ? <div className="closeImage">X</div> : null}
    {index%2 === 0 ? <><br /><br /></> : null}
  </div>
}

export default GameCard;